// Amazon-style review system with promotional codes
export interface Review {
  id: string;
  productId: string;
  userId: string;
  userName: string;
  userAvatar?: string;
  rating: number; // 1-5 stars
  title: string;
  comment: string;
  dateCreated: Date;
  verified: boolean; // Verified purchase
  helpful: number; // Number of helpful votes
  notHelpful: number; // Number of not helpful votes
  images?: string[]; // Customer uploaded images
  videos?: string[]; // Customer uploaded videos
  vineCustomer?: boolean; // Amazon Vine customer
  earlyReviewer?: boolean; // Early reviewer program
  topReviewer?: boolean; // Top reviewer badge
  incentivized?: boolean; // Review incentivized (promo code)
  promoCodeUsed?: string; // Promo code used for purchase
}

export interface PromoCode {
  id: string;
  code: string;
  description: string;
  discount: number; // Percentage or amount
  discountType: 'percentage' | 'fixed';
  minOrderAmount?: number;
  maxDiscount?: number;
  validFrom: Date;
  validTo: Date;
  usageLimit: number;
  usedCount: number;
  active: boolean;
  applicableProducts?: string[]; // Product IDs, empty means all products
  firstTimeOnly?: boolean;
  reviewIncentive?: {
    enabled: boolean;
    additionalDiscount: number;
    description: string;
  };
}

export interface ReviewSummary {
  averageRating: number;
  totalReviews: number;
  ratingDistribution: { [key: number]: number };
  recommendationPercentage: number;
  verifiedPurchasePercentage: number;
  incentivizedReviewPercentage: number;
}

