// Helper functions - Amazon style
export const getReviewsForProduct = (productId: string): Review[] => {
  return reviewsData.filter(review => review.productId === productId);
};

export const getAverageRating = (productId: string): number => {
  const reviews = getReviewsForProduct(productId);
  if (reviews.length === 0) return 0;
  
  const totalRating = reviews.reduce((sum, review) => sum + review.rating, 0);
  return Math.round((totalRating / reviews.length) * 10) / 10; // Round to 1 decimal
};

export const getReviewCount = (productId: string): number => {
  return getReviewsForProduct(productId).length;
};

export const getRatingDistribution = (productId: string): { [key: number]: number } => {
  const reviews = getReviewsForProduct(productId);
  const distribution: { [key: number]: number } = { 5: 0, 4: 0, 3: 0, 2: 0, 1: 0 };
  
  reviews.forEach(review => {
    distribution[review.rating]++;
  });
  
  return distribution;
};

export const getRecommendationPercentage = (productId: string): number => {
  const reviews = getReviewsForProduct(productId);
  if (reviews.length === 0) return 0;
  
  const positiveReviews = reviews.filter(review => review.rating >= 4).length;
  return Math.round((positiveReviews / reviews.length) * 100);
};

export const getVerifiedPurchasePercentage = (productId: string): number => {
  const reviews = getReviewsForProduct(productId);
  if (reviews.length === 0) return 0;
  
  const verifiedReviews = reviews.filter(review => review.verified).length;
  return Math.round((verifiedReviews / reviews.length) * 100);
};

export const getIncentivizedReviewPercentage = (productId: string): number => {
  const reviews = getReviewsForProduct(productId);
  if (reviews.length === 0) return 0;
  
  const incentivizedReviews = reviews.filter(review => review.incentivized).length;
  return Math.round((incentivizedReviews / reviews.length) * 100);
};

export const getHelpfulnessRatio = (review: Review): number => {
  const total = review.helpful + review.notHelpful;
  if (total === 0) return 0;
  return Math.round((review.helpful / total) * 100);
};

export const getReviewSummary = (productId: string): ReviewSummary => {
  return {
    averageRating: getAverageRating(productId),
    totalReviews: getReviewCount(productId),
    ratingDistribution: getRatingDistribution(productId),
    recommendationPercentage: getRecommendationPercentage(productId),
    verifiedPurchasePercentage: getVerifiedPurchasePercentage(productId),
    incentivizedReviewPercentage: getIncentivizedReviewPercentage(productId)
  };
};

// Sort options for reviews
export type ReviewSortOption = 
  | 'helpful' 
  | 'newest' 
  | 'oldest' 
  | 'highest' 
  | 'lowest' 
  | 'verified'
  | 'most_images';

export const sortReviews = (reviews: Review[], sortBy: ReviewSortOption): Review[] => {
  const sorted = [...reviews];
  
  switch (sortBy) {
    case 'helpful':
      return sorted.sort((a, b) => b.helpful - a.helpful);
    case 'newest':
      return sorted.sort((a, b) => new Date(b.dateCreated).getTime() - new Date(a.dateCreated).getTime());
    case 'oldest':
      return sorted.sort((a, b) => new Date(a.dateCreated).getTime() - new Date(b.dateCreated).getTime());
    case 'highest':
      return sorted.sort((a, b) => b.rating - a.rating);
    case 'lowest':
      return sorted.sort((a, b) => a.rating - b.rating);
    case 'verified':
      return sorted.sort((a, b) => (b.verified ? 1 : 0) - (a.verified ? 1 : 0));
    case 'most_images':
      return sorted.sort((a, b) => (b.images?.length || 0) - (a.images?.length || 0));
    default:
      return sorted;
  }
};

// Promo code functions
export const getActivePromoCodes = (): PromoCode[] => {
  const now = new Date();
  return promoCodes.filter(promo => 
    promo.active && 
    now >= promo.validFrom && 
    now <= promo.validTo &&
    promo.usedCount < promo.usageLimit
  );
};

export const validatePromoCode = (code: string, orderAmount: number, isFirstTime: boolean = false): {
  valid: boolean;
  promoCode?: PromoCode;
  discount: number;
  error?: string;
} => {
  const promo = promoCodes.find(p => p.code.toLowerCase() === code.toLowerCase());
  
  if (!promo) {
    return { valid: false, discount: 0, error: 'Invalid promo code' };
  }
  
  if (!promo.active) {
    return { valid: false, discount: 0, error: 'Promo code is no longer active' };
  }
  
  const now = new Date();
  if (now < promo.validFrom || now > promo.validTo) {
    return { valid: false, discount: 0, error: 'Promo code has expired' };
  }
  
  if (promo.usedCount >= promo.usageLimit) {
    return { valid: false, discount: 0, error: 'Promo code usage limit exceeded' };
  }
  
  if (promo.minOrderAmount && orderAmount < promo.minOrderAmount) {
    return { 
      valid: false, 
      discount: 0, 
      error: `Minimum order amount ₹${promo.minOrderAmount} required` 
    };
  }
  
  if (promo.firstTimeOnly && !isFirstTime) {
    return { valid: false, discount: 0, error: 'This offer is only for first-time customers' };
  }
  
  let discount = 0;
  if (promo.discountType === 'percentage') {
    discount = (orderAmount * promo.discount) / 100;
    if (promo.maxDiscount) {
      discount = Math.min(discount, promo.maxDiscount);
    }
  } else {
    discount = promo.discount;
  }
  
  return { valid: true, promoCode: promo, discount: Math.round(discount) };
};

export const getPromoCodeByProduct = (productId: string): PromoCode[] => {
  return getActivePromoCodes().filter(promo => 
    !promo.applicableProducts || 
    promo.applicableProducts.length === 0 || 
    promo.applicableProducts.includes(productId)
  );
};
