'use client';

import React, { useState, useMemo } from 'react';
import StarRating, { RatingSummary } from './StarRating';
import { Review, ReviewSortOption, sortReviews, getHelpfulnessRatio } from '../data/reviews';

interface ReviewsDisplayProps {
  productId: string;
  reviews: Review[];
  averageRating: number;
  totalReviews: number;
  ratingDistribution: { [key: number]: number };
  recommendationPercentage: number;
  className?: string;
}

const ReviewsDisplay: React.FC<ReviewsDisplayProps> = ({
  productId,
  reviews,
  averageRating,
  totalReviews,
  ratingDistribution,
  recommendationPercentage,
  className = ''
}) => {
  const [sortBy, setSortBy] = useState<ReviewSortOption>('helpful');
  const [filterByRating, setFilterByRating] = useState<number | null>(null);
  const [showOnlyVerified, setShowOnlyVerified] = useState(false);
  const [showOnlyImages, setShowOnlyImages] = useState(false);
  const [expandedReviews, setExpandedReviews] = useState<Set<string>>(new Set());
  const [showAllReviews, setShowAllReviews] = useState(false);

  // Filter and sort reviews
  const filteredAndSortedReviews = useMemo(() => {
    let filtered = [...reviews];

    // Apply filters
    if (filterByRating) {
      filtered = filtered.filter(review => review.rating === filterByRating);
    }

    if (showOnlyVerified) {
      filtered = filtered.filter(review => review.verified);
    }

    if (showOnlyImages) {
      filtered = filtered.filter(review => review.images && review.images.length > 0);
    }

    // Sort reviews
    return sortReviews(filtered, sortBy);
  }, [reviews, sortBy, filterByRating, showOnlyVerified, showOnlyImages]);

  // Show limited reviews initially
  const displayedReviews = showAllReviews 
    ? filteredAndSortedReviews 
    : filteredAndSortedReviews.slice(0, 5);

  const toggleReviewExpansion = (reviewId: string) => {
    const newExpanded = new Set(expandedReviews);
    if (newExpanded.has(reviewId)) {
      newExpanded.delete(reviewId);
    } else {
      newExpanded.add(reviewId);
    }
    setExpandedReviews(newExpanded);
  };

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    }).format(new Date(date));
  };

  const getCharacterLimit = () => 300; // Amazon-style character limit

  const shouldTruncate = (text: string) => {
    return text.length > getCharacterLimit();
  };

  const getTruncatedText = (text: string) => {
    if (text.length <= getCharacterLimit()) return text;
    return text.substring(0, getCharacterLimit()) + '...';
  };

  if (reviews.length === 0) {
    return (
      <div className={`bg-white rounded-lg border border-gray-200 p-8 text-center ${className}`}>
        <div className="text-gray-500 text-lg mb-4">No reviews yet</div>
        <div className="text-gray-400">Be the first to review this product!</div>
      </div>
    );
  }

  return (
    <div className={`space-y-6 ${className}`}>
      {/* Rating Summary */}
      <RatingSummary
        averageRating={averageRating}
        totalReviews={totalReviews}
        ratingDistribution={ratingDistribution}
        recommendationPercentage={recommendationPercentage}
      />

      {/* Filters and Sort */}
      <div className="bg-white border border-gray-200 rounded-lg p-4">
        <div className="flex flex-wrap items-center gap-4 mb-4">
          <div className="text-lg font-semibold text-gray-900">
            Customer Reviews ({filteredAndSortedReviews.length})
          </div>
        </div>

        <div className="flex flex-wrap items-center gap-4">
          {/* Sort Dropdown */}
          <div className="flex items-center gap-2">
            <label className="text-sm font-medium text-gray-700">Sort by:</label>
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as ReviewSortOption)}
              className="border border-gray-300 rounded-md px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="helpful">Most Helpful</option>
              <option value="newest">Newest</option>
              <option value="oldest">Oldest</option>
              <option value="highest">Highest Rating</option>
              <option value="lowest">Lowest Rating</option>
              <option value="verified">Verified Purchases</option>
              <option value="most_images">With Images</option>
            </select>
          </div>

          {/* Rating Filter */}
          <div className="flex items-center gap-2">
            <label className="text-sm font-medium text-gray-700">Filter by rating:</label>
            <select
              value={filterByRating || ''}
              onChange={(e) => setFilterByRating(e.target.value ? parseInt(e.target.value) : null)}
              className="border border-gray-300 rounded-md px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="">All ratings</option>
              <option value="5">5 stars</option>
              <option value="4">4 stars</option>
              <option value="3">3 stars</option>
              <option value="2">2 stars</option>
              <option value="1">1 star</option>
            </select>
          </div>

          {/* Additional Filters */}
          <div className="flex items-center gap-4">
            <label className="flex items-center gap-2 text-sm">
              <input
                type="checkbox"
                checked={showOnlyVerified}
                onChange={(e) => setShowOnlyVerified(e.target.checked)}
                className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
              />
              Verified purchases only
            </label>

            <label className="flex items-center gap-2 text-sm">
              <input
                type="checkbox"
                checked={showOnlyImages}
                onChange={(e) => setShowOnlyImages(e.target.checked)}
                className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
              />
              With images only
            </label>
          </div>
        </div>
      </div>

      {/* Reviews List */}
      <div className="space-y-4">
        {displayedReviews.map((review) => {
          const isExpanded = expandedReviews.has(review.id);
          const shouldShowSeeMore = shouldTruncate(review.comment);
          const helpfulnessRatio = getHelpfulnessRatio(review);

          return (
            <div key={review.id} className="bg-white border border-gray-200 rounded-lg p-6">
              {/* Review Header */}
              <div className="flex items-start justify-between mb-4">
                <div className="flex items-center gap-3">
                  {/* User Avatar */}
                  <div className="w-8 h-8 bg-gray-300 rounded-full flex items-center justify-center">
                    {review.userAvatar ? (
                      <img
                        src={review.userAvatar}
                        alt={review.userName}
                        className="w-8 h-8 rounded-full"
                      />
                    ) : (
                      <span className="text-sm font-medium text-gray-600">
                        {review.userName.charAt(0).toUpperCase()}
                      </span>
                    )}
                  </div>

                  <div>
                    <div className="flex items-center gap-2">
                      <span className="font-medium text-gray-900">{review.userName}</span>
                      {review.verified && (
                        <span className="bg-orange-100 text-orange-800 text-xs px-2 py-1 rounded-full">
                          Verified Purchase
                        </span>
                      )}
                      {review.topReviewer && (
                        <span className="bg-purple-100 text-purple-800 text-xs px-2 py-1 rounded-full">
                          Top Reviewer
                        </span>
                      )}
                      {review.vineCustomer && (
                        <span className="bg-green-100 text-green-800 text-xs px-2 py-1 rounded-full">
                          Vine Customer
                        </span>
                      )}
                      {review.earlyReviewer && (
                        <span className="bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-full">
                          Early Reviewer
                        </span>
                      )}
                    </div>
                    <div className="text-sm text-gray-600">{formatDate(review.dateCreated)}</div>
                  </div>
                </div>

                {/* Promo Code Badge */}
                {review.promoCodeUsed && (
                  <div className="bg-yellow-100 text-yellow-800 text-xs px-2 py-1 rounded-full">
                    Used: {review.promoCodeUsed}
                  </div>
                )}
              </div>

              {/* Rating and Title */}
              <div className="mb-3">
                <div className="flex items-center gap-2 mb-2">
                  <StarRating rating={review.rating} size="sm" />
                  <span className="font-semibold text-gray-900">{review.title}</span>
                </div>
              </div>

              {/* Review Content */}
              <div className="mb-4">
                <p className="text-gray-700 leading-relaxed">
                  {shouldShowSeeMore && !isExpanded 
                    ? getTruncatedText(review.comment)
                    : review.comment
                  }
                </p>

                {/* See More / See Less Toggle */}
                {shouldShowSeeMore && (
                  <button
                    onClick={() => toggleReviewExpansion(review.id)}
                    className="mt-2 text-blue-600 hover:text-blue-800 hover:underline text-sm font-medium focus:outline-none"
                  >
                    {isExpanded ? 'See less' : 'See more'}
                  </button>
                )}
              </div>

              {/* Review Images */}
              {review.images && review.images.length > 0 && (
                <div className="mb-4">
                  <div className="flex gap-2 overflow-x-auto pb-2">
                    {review.images.map((image, index) => (
                      <img
                        key={index}
                        src={image}
                        alt={`Review image ${index + 1}`}
                        className="w-20 h-20 object-cover rounded-lg border border-gray-200 flex-shrink-0 cursor-pointer hover:opacity-80"
                      />
                    ))}
                  </div>
                </div>
              )}

              {/* Incentivized Review Notice */}
              {review.incentivized && (
                <div className="mb-4 p-3 bg-blue-50 border border-blue-200 rounded-lg">
                  <div className="flex items-center gap-2">
                    <svg className="w-4 h-4 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                    </svg>
                    <span className="text-sm text-blue-800">
                      This review was written using a promotional discount.
                    </span>
                  </div>
                </div>
              )}

              {/* Review Actions */}
              <div className="flex items-center justify-between pt-4 border-t border-gray-200">
                <div className="flex items-center gap-4">
                  {/* Helpful Buttons */}
                  <div className="flex items-center gap-2">
                    <span className="text-sm text-gray-600">Helpful?</span>
                    <button className="text-sm text-blue-600 hover:text-blue-800 hover:underline">
                      👍 Yes ({review.helpful})
                    </button>
                    <button className="text-sm text-blue-600 hover:text-blue-800 hover:underline">
                      👎 No ({review.notHelpful})
                    </button>
                  </div>

                  {/* Helpfulness Ratio */}
                  {helpfulnessRatio > 0 && (
                    <div className="text-sm text-gray-500">
                      {helpfulnessRatio}% found this helpful
                    </div>
                  )}
                </div>

                {/* Report Button */}
                <button className="text-sm text-gray-500 hover:text-gray-700">
                  Report
                </button>
              </div>
            </div>
          );
        })}
      </div>

      {/* See More Reviews Button */}
      {!showAllReviews && filteredAndSortedReviews.length > 5 && (
        <div className="text-center">
          <button
            onClick={() => setShowAllReviews(true)}
            className="bg-white border border-gray-300 text-gray-700 px-6 py-3 rounded-lg hover:bg-gray-50 hover:border-gray-400 transition-colors font-medium"
          >
            See all {filteredAndSortedReviews.length} reviews
          </button>
        </div>
      )}

      {/* Show Less Button */}
      {showAllReviews && filteredAndSortedReviews.length > 5 && (
        <div className="text-center">
          <button
            onClick={() => setShowAllReviews(false)}
            className="bg-white border border-gray-300 text-gray-700 px-6 py-3 rounded-lg hover:bg-gray-50 hover:border-gray-400 transition-colors font-medium"
          >
            Show less reviews
          </button>
        </div>
      )}

      {/* No Results Message */}
      {filteredAndSortedReviews.length === 0 && reviews.length > 0 && (
        <div className="bg-white border border-gray-200 rounded-lg p-8 text-center">
          <div className="text-gray-500 text-lg mb-2">No reviews match your filters</div>
          <div className="text-gray-400 mb-4">Try adjusting your filter criteria</div>
          <button
            onClick={() => {
              setFilterByRating(null);
              setShowOnlyVerified(false);
              setShowOnlyImages(false);
            }}
            className="text-blue-600 hover:text-blue-800 hover:underline font-medium"
          >
            Clear all filters
          </button>
        </div>
      )}
    </div>
  );
};

export default ReviewsDisplay;
