'use client';

import React, { useState } from 'react';
import StarRating from './StarRating';
import { Review } from '../data/reviews';

interface WriteReviewFormProps {
  productId: string;
  productName: string;
  onReviewSubmitted?: (review: Partial<Review>) => void;
  onCancel?: () => void;
  className?: string;
}

const WriteReviewForm: React.FC<WriteReviewFormProps> = ({
  productId,
  productName,
  onReviewSubmitted,
  onCancel,
  className = ''
}) => {
  const [rating, setRating] = useState<number>(0);
  const [title, setTitle] = useState('');
  const [comment, setComment] = useState('');
  const [userName, setUserName] = useState('');
  const [images, setImages] = useState<FileList | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [errors, setErrors] = useState<{[key: string]: string}>({});

  const validateForm = () => {
    const newErrors: {[key: string]: string} = {};

    if (!userName.trim()) {
      newErrors.userName = 'Name is required';
    }

    if (rating === 0) {
      newErrors.rating = 'Please select a rating';
    }

    if (!title.trim()) {
      newErrors.title = 'Review title is required';
    } else if (title.length < 10) {
      newErrors.title = 'Title should be at least 10 characters';
    } else if (title.length > 100) {
      newErrors.title = 'Title should not exceed 100 characters';
    }

    if (!comment.trim()) {
      newErrors.comment = 'Review comment is required';
    } else if (comment.length < 20) {
      newErrors.comment = 'Comment should be at least 20 characters';
    } else if (comment.length > 1000) {
      newErrors.comment = 'Comment should not exceed 1000 characters';
    }

    if (images && images.length > 5) {
      newErrors.images = 'Maximum 5 images allowed';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);

    try {
      // Simulate API call delay
      await new Promise(resolve => setTimeout(resolve, 1000));

      const newReview: Partial<Review> = {
        id: `rev-${Date.now()}`,
        productId,
        userId: `user-${Date.now()}`,
        userName: userName.trim(),
        rating,
        title: title.trim(),
        comment: comment.trim(),
        dateCreated: new Date(),
        verified: true, // Assume verified purchase
        helpful: 0,
        notHelpful: 0,
        images: images ? Array.from(images).map(file => URL.createObjectURL(file)) : undefined
      };

      onReviewSubmitted?.(newReview);
      
      // Reset form
      setRating(0);
      setTitle('');
      setComment('');
      setUserName('');
      setImages(null);
      setErrors({});
      
    } catch (error) {
      console.error('Error submitting review:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 5) {
      setErrors({ ...errors, images: 'Maximum 5 images allowed' });
      return;
    }
    setImages(files);
    if (errors.images) {
      const newErrors = { ...errors };
      delete newErrors.images;
      setErrors(newErrors);
    }
  };

  const removeImage = (index: number) => {
    if (images) {
      const filesArray = Array.from(images);
      filesArray.splice(index, 1);
      
      // Create new FileList-like object
      const dt = new DataTransfer();
      filesArray.forEach(file => dt.items.add(file));
      setImages(dt.files);
    }
  };

  const getRatingText = (rating: number) => {
    switch (rating) {
      case 1: return 'Poor';
      case 2: return 'Fair';
      case 3: return 'Good';
      case 4: return 'Very Good';
      case 5: return 'Excellent';
      default: return 'Select a rating';
    }
  };

  return (
    <div className={`bg-white border border-gray-200 rounded-lg p-6 ${className}`}>
      {/* Header */}
      <div className="mb-6">
        <h3 className="text-xl font-semibold text-gray-900 mb-2">
          Write a Review
        </h3>
        <p className="text-gray-600">
          Share your experience with <span className="font-medium">{productName}</span>
        </p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Name Field */}
        <div>
          <label htmlFor="userName" className="block text-sm font-medium text-gray-700 mb-2">
            Your Name *
          </label>
          <input
            type="text"
            id="userName"
            value={userName}
            onChange={(e) => {
              setUserName(e.target.value);
              if (errors.userName) {
                const newErrors = { ...errors };
                delete newErrors.userName;
                setErrors(newErrors);
              }
            }}
            className={`w-full border rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 ${
              errors.userName ? 'border-red-500' : 'border-gray-300'
            }`}
            placeholder="Enter your name"
          />
          {errors.userName && (
            <p className="text-red-600 text-sm mt-1">{errors.userName}</p>
          )}
        </div>

        {/* Rating Section */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Overall Rating *
          </label>
          <div className="flex items-center gap-3">
            <StarRating
              rating={rating}
              interactive={true}
              onRatingChange={setRating}
              size="lg"
              className={errors.rating ? 'border border-red-500 rounded p-2' : ''}
            />
            <span className="text-lg font-medium text-gray-700">
              {getRatingText(rating)}
            </span>
          </div>
          {errors.rating && (
            <p className="text-red-600 text-sm mt-1">{errors.rating}</p>
          )}
        </div>

        {/* Title Field */}
        <div>
          <label htmlFor="title" className="block text-sm font-medium text-gray-700 mb-2">
            Review Title *
          </label>
          <input
            type="text"
            id="title"
            value={title}
            onChange={(e) => {
              setTitle(e.target.value);
              if (errors.title) {
                const newErrors = { ...errors };
                delete newErrors.title;
                setErrors(newErrors);
              }
            }}
            className={`w-full border rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 ${
              errors.title ? 'border-red-500' : 'border-gray-300'
            }`}
            placeholder="Summarize your experience (e.g., 'Great taste and quality!')"
            maxLength={100}
          />
          <div className="flex justify-between mt-1">
            {errors.title ? (
              <p className="text-red-600 text-sm">{errors.title}</p>
            ) : (
              <div></div>
            )}
            <span className="text-sm text-gray-500">{title.length}/100</span>
          </div>
        </div>

        {/* Comment Field */}
        <div>
          <label htmlFor="comment" className="block text-sm font-medium text-gray-700 mb-2">
            Detailed Review *
          </label>
          <textarea
            id="comment"
            value={comment}
            onChange={(e) => {
              setComment(e.target.value);
              if (errors.comment) {
                const newErrors = { ...errors };
                delete newErrors.comment;
                setErrors(newErrors);
              }
            }}
            rows={6}
            className={`w-full border rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none ${
              errors.comment ? 'border-red-500' : 'border-gray-300'
            }`}
            placeholder="Tell others about your experience with this product. What did you like or dislike? How was the quality, taste, packaging, etc.?"
            maxLength={1000}
          />
          <div className="flex justify-between mt-1">
            {errors.comment ? (
              <p className="text-red-600 text-sm">{errors.comment}</p>
            ) : (
              <div></div>
            )}
            <span className="text-sm text-gray-500">{comment.length}/1000</span>
          </div>
        </div>

        {/* Image Upload */}
        <div>
          <label htmlFor="images" className="block text-sm font-medium text-gray-700 mb-2">
            Add Images (Optional)
          </label>
          <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-gray-400 transition-colors">
            <input
              type="file"
              id="images"
              multiple
              accept="image/*"
              onChange={handleImageChange}
              className="hidden"
            />
            <label htmlFor="images" className="cursor-pointer">
              <svg className="w-12 h-12 text-gray-400 mx-auto mb-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
              </svg>
              <p className="text-gray-600">Click to upload images</p>
              <p className="text-sm text-gray-500 mt-1">Max 5 images, up to 5MB each</p>
            </label>
          </div>
          {errors.images && (
            <p className="text-red-600 text-sm mt-1">{errors.images}</p>
          )}
        </div>

        {/* Image Preview */}
        {images && images.length > 0 && (
          <div>
            <p className="text-sm font-medium text-gray-700 mb-2">
              Selected Images ({images.length}/5)
            </p>
            <div className="flex gap-2 flex-wrap">
              {Array.from(images).map((file, index) => (
                <div key={index} className="relative">
                  <img
                    src={URL.createObjectURL(file)}
                    alt={`Preview ${index + 1}`}
                    className="w-20 h-20 object-cover rounded-lg border border-gray-200"
                  />
                  <button
                    type="button"
                    onClick={() => removeImage(index)}
                    className="absolute -top-1 -right-1 bg-red-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs hover:bg-red-600"
                  >
                    ×
                  </button>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Review Guidelines */}
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
          <h4 className="font-medium text-blue-900 mb-2">Review Guidelines</h4>
          <ul className="text-sm text-blue-800 space-y-1">
            <li>• Be honest and specific about your experience</li>
            <li>• Focus on the product features, quality, and value</li>
            <li>• Include both positive and negative aspects</li>
            <li>• Avoid inappropriate language or personal information</li>
            <li>• Only review products you have actually purchased</li>
          </ul>
        </div>

        {/* Action Buttons */}
        <div className="flex gap-4 pt-4 border-t border-gray-200">
          {onCancel && (
            <button
              type="button"
              onClick={onCancel}
              className="flex-1 border border-gray-300 text-gray-700 py-2 px-4 rounded-lg hover:bg-gray-50 transition-colors"
              disabled={isSubmitting}
            >
              Cancel
            </button>
          )}
          <button
            type="submit"
            disabled={isSubmitting || rating === 0}
            className="flex-1 bg-blue-600 text-white py-2 px-4 rounded-lg hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
          >
            {isSubmitting ? (
              <span className="flex items-center justify-center gap-2">
                <svg className="animate-spin w-4 h-4" fill="none" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                  <path className="opacity-75" fill="currentColor" d="m4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                </svg>
                Submitting...
              </span>
            ) : (
              'Submit Review'
            )}
          </button>
        </div>
      </form>

      {/* Review Incentive Notice */}
      <div className="mt-6 p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
        <div className="flex items-start gap-3">
          <svg className="w-5 h-5 text-yellow-600 mt-0.5" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
          </svg>
          <div>
            <h5 className="font-medium text-yellow-900">Earn Rewards for Reviews!</h5>
            <p className="text-yellow-800 text-sm mt-1">
              Get exclusive discount codes when you write detailed, helpful reviews. 
              Verified purchase reviews earn even better rewards!
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default WriteReviewForm;
