'use client';

import React, { useState } from 'react';

interface StarRatingProps {
  rating: number;
  maxRating?: number;
  size?: 'sm' | 'md' | 'lg' | 'xl';
  interactive?: boolean;
  showNumber?: boolean;
  showCount?: boolean;
  reviewCount?: number;
  onRatingChange?: (rating: number) => void;
  className?: string;
  fillColor?: string;
  emptyColor?: string;
  hoverColor?: string;
}

const StarRating: React.FC<StarRatingProps> = ({
  rating,
  maxRating = 5,
  size = 'md',
  interactive = false,
  showNumber = false,
  showCount = false,
  reviewCount = 0,
  onRatingChange,
  className = '',
  fillColor = '#FFA500', // Amazon orange/yellow
  emptyColor = '#E5E5E5',
  hoverColor = '#FFB84D'
}) => {
  const [hoverRating, setHoverRating] = useState<number>(0);
  const [selectedRating, setSelectedRating] = useState<number>(rating);

  const getSizeClasses = () => {
    switch (size) {
      case 'sm':
        return 'w-3 h-3';
      case 'md':
        return 'w-4 h-4';
      case 'lg':
        return 'w-5 h-5';
      case 'xl':
        return 'w-6 h-6';
      default:
        return 'w-4 h-4';
    }
  };

  const getTextSize = () => {
    switch (size) {
      case 'sm':
        return 'text-sm';
      case 'md':
        return 'text-base';
      case 'lg':
        return 'text-lg';
      case 'xl':
        return 'text-xl';
      default:
        return 'text-base';
    }
  };

  const handleStarClick = (starValue: number) => {
    if (interactive) {
      setSelectedRating(starValue);
      onRatingChange?.(starValue);
    }
  };

  const handleMouseEnter = (starValue: number) => {
    if (interactive) {
      setHoverRating(starValue);
    }
  };

  const handleMouseLeave = () => {
    if (interactive) {
      setHoverRating(0);
    }
  };

  const getStarFill = (starIndex: number) => {
    const currentRating = interactive ? (hoverRating || selectedRating) : rating;
    const starValue = starIndex + 1;
    
    if (currentRating >= starValue) {
      return '100%';
    } else if (currentRating > starIndex && currentRating < starValue) {
      return `${(currentRating - starIndex) * 100}%`;
    }
    return '0%';
  };

  const getStarColor = (starIndex: number) => {
    if (interactive && hoverRating > 0) {
      return hoverRating > starIndex ? hoverColor : emptyColor;
    }
    return fillColor;
  };

  return (
    <div className={`flex items-center gap-1 ${className}`}>
      <div className="flex items-center">
        {Array.from({ length: maxRating }, (_, index) => (
          <div
            key={index}
            className={`relative ${getSizeClasses()} ${
              interactive ? 'cursor-pointer' : ''
            }`}
            onClick={() => handleStarClick(index + 1)}
            onMouseEnter={() => handleMouseEnter(index + 1)}
            onMouseLeave={handleMouseLeave}
          >
            {/* Background star (empty) */}
            <svg
              className={`${getSizeClasses()} absolute inset-0`}
              fill={emptyColor}
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
            </svg>
            
            {/* Foreground star (filled) */}
            <div
              className="absolute inset-0 overflow-hidden"
              style={{ width: getStarFill(index) }}
            >
              <svg
                className={getSizeClasses()}
                fill={getStarColor(index)}
                viewBox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
              </svg>
            </div>
          </div>
        ))}
      </div>
      
      {/* Rating number and count */}
      <div className="flex items-center gap-1">
        {showNumber && (
          <span className={`font-medium text-gray-700 ${getTextSize()}`}>
            {rating.toFixed(1)}
          </span>
        )}
        
        {showCount && reviewCount > 0 && (
          <>
            <span className="text-gray-400">•</span>
            <span className={`text-blue-600 hover:text-blue-800 hover:underline cursor-pointer ${getTextSize()}`}>
              {reviewCount} {reviewCount === 1 ? 'review' : 'reviews'}
            </span>
          </>
        )}
      </div>
    </div>
  );
};

// Rating Summary Component - Amazon style
interface RatingSummaryProps {
  averageRating: number;
  totalReviews: number;
  ratingDistribution: { [key: number]: number };
  recommendationPercentage: number;
  className?: string;
}

export const RatingSummary: React.FC<RatingSummaryProps> = ({
  averageRating,
  totalReviews,
  ratingDistribution,
  recommendationPercentage,
  className = ''
}) => {
  const getPercentage = (count: number) => {
    return totalReviews > 0 ? Math.round((count / totalReviews) * 100) : 0;
  };

  return (
    <div className={`bg-white border border-gray-200 rounded-lg p-6 ${className}`}>
      {/* Main Rating Display */}
      <div className="flex items-center gap-4 mb-6">
        <div className="text-center">
          <div className="text-4xl font-bold text-gray-900 mb-1">
            {averageRating.toFixed(1)}
          </div>
          <StarRating rating={averageRating} size="lg" showNumber={false} />
          <div className="text-sm text-gray-600 mt-1">
            {totalReviews} {totalReviews === 1 ? 'review' : 'reviews'}
          </div>
        </div>
        
        <div className="flex-1">
          <div className="text-lg font-semibold text-gray-900 mb-2">
            {recommendationPercentage}% recommend this product
          </div>
          <div className="text-sm text-gray-600">
            Based on verified purchases
          </div>
        </div>
      </div>

      {/* Rating Distribution */}
      <div className="space-y-3">
        <div className="text-sm font-semibold text-gray-900 mb-3">
          Rating breakdown
        </div>
        
        {[5, 4, 3, 2, 1].map((stars) => {
          const count = ratingDistribution[stars] || 0;
          const percentage = getPercentage(count);
          
          return (
            <div key={stars} className="flex items-center gap-3">
              <div className="flex items-center gap-1 w-20">
                <span className="text-sm font-medium">{stars}</span>
                <svg className="w-3 h-3 text-yellow-400" fill="currentColor" viewBox="0 0 20 20">
                  <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                </svg>
              </div>
              
              <div className="flex-1 h-4 bg-gray-200 rounded-full overflow-hidden">
                <div
                  className="h-full bg-yellow-400 transition-all duration-300"
                  style={{ width: `${percentage}%` }}
                />
              </div>
              
              <div className="w-12 text-right">
                <span className="text-sm text-blue-600 hover:underline cursor-pointer">
                  {count > 0 ? count : ''}
                </span>
              </div>
              
              <div className="w-8 text-right">
                <span className="text-sm text-gray-600">
                  {percentage > 0 ? `${percentage}%` : ''}
                </span>
              </div>
            </div>
          );
        })}
      </div>

      {/* Quick Stats */}
      <div className="mt-6 pt-4 border-t border-gray-200">
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <span className="text-gray-600">Most helpful:</span>
            <span className="ml-1 text-blue-600 hover:underline cursor-pointer">
              5-star reviews
            </span>
          </div>
          <div>
            <span className="text-gray-600">Recent:</span>
            <span className="ml-1 text-blue-600 hover:underline cursor-pointer">
              Last 30 days
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

// Compact Rating Display for Product Cards
interface CompactRatingProps {
  rating: number;
  reviewCount: number;
  showReviewCount?: boolean;
  className?: string;
}

export const CompactRating: React.FC<CompactRatingProps> = ({
  rating,
  reviewCount,
  showReviewCount = true,
  className = ''
}) => {
  return (
    <div className={`flex items-center gap-1 ${className}`}>
      <StarRating 
        rating={rating} 
        size="sm" 
        showNumber={false}
        showCount={false}
      />
      <span className="text-sm font-medium text-gray-700">
        {rating.toFixed(1)}
      </span>
      {showReviewCount && reviewCount > 0 && (
        <>
          <span className="text-gray-400 text-sm">•</span>
          <span className="text-sm text-blue-600 hover:underline cursor-pointer">
            ({reviewCount})
          </span>
        </>
      )}
    </div>
  );
};

export default StarRating;
