'use client';

import React, { useState, useEffect } from 'react';
import { PromoCode, getActivePromoCodes, validatePromoCode, getPromoCodeByProduct } from '../data/reviews';

interface PromoCodeDisplayProps {
  productId?: string;
  orderAmount?: number;
  isFirstTime?: boolean;
  onPromoApplied?: (promoCode: PromoCode, discount: number) => void;
  className?: string;
}

const PromoCodeDisplay: React.FC<PromoCodeDisplayProps> = ({
  productId,
  orderAmount = 0,
  isFirstTime = false,
  onPromoApplied,
  className = ''
}) => {
  const [promoCodes, setPromoCodes] = useState<PromoCode[]>([]);
  const [enteredCode, setEnteredCode] = useState('');
  const [appliedPromo, setAppliedPromo] = useState<PromoCode | null>(null);
  const [discount, setDiscount] = useState(0);
  const [error, setError] = useState('');
  const [showAllCodes, setShowAllCodes] = useState(false);
  const [copiedCode, setCopiedCode] = useState<string | null>(null);

  useEffect(() => {
    const codes = productId 
      ? getPromoCodeByProduct(productId)
      : getActivePromoCodes();
    setPromoCodes(codes);
  }, [productId]);

  const handleApplyCode = () => {
    if (!enteredCode.trim()) {
      setError('Please enter a promo code');
      return;
    }

    const validation = validatePromoCode(enteredCode.trim(), orderAmount, isFirstTime);
    
    if (validation.valid && validation.promoCode) {
      setAppliedPromo(validation.promoCode);
      setDiscount(validation.discount);
      setError('');
      onPromoApplied?.(validation.promoCode, validation.discount);
    } else {
      setError(validation.error || 'Invalid promo code');
      setAppliedPromo(null);
      setDiscount(0);
    }
  };

  const handleRemoveCode = () => {
    setAppliedPromo(null);
    setDiscount(0);
    setEnteredCode('');
    setError('');
    onPromoApplied?.(null as any, 0);
  };

  const copyToClipboard = (code: string) => {
    navigator.clipboard.writeText(code);
    setCopiedCode(code);
    setTimeout(() => setCopiedCode(null), 2000);
  };

  const formatDiscount = (promo: PromoCode) => {
    if (promo.discountType === 'percentage') {
      return `${promo.discount}% OFF`;
    } else {
      return `₹${promo.discount} OFF`;
    }
  };

  const getDiscountDescription = (promo: PromoCode) => {
    let description = promo.description;
    
    if (promo.minOrderAmount) {
      description += ` (Min order: ₹${promo.minOrderAmount})`;
    }
    
    if (promo.maxDiscount && promo.discountType === 'percentage') {
      description += ` (Max discount: ₹${promo.maxDiscount})`;
    }
    
    return description;
  };

  const isCodeExpiringSoon = (promo: PromoCode) => {
    const now = new Date();
    const validTo = new Date(promo.validTo);
    const daysLeft = Math.ceil((validTo.getTime() - now.getTime()) / (1000 * 60 * 60 * 24));
    return daysLeft <= 7;
  };

  const getUsagePercentage = (promo: PromoCode) => {
    return Math.round((promo.usedCount / promo.usageLimit) * 100);
  };

  const displayedCodes = showAllCodes ? promoCodes : promoCodes.slice(0, 3);

  return (
    <div className={`bg-white border border-gray-200 rounded-lg p-6 ${className}`}>
      {/* Header */}
      <div className="flex items-center gap-2 mb-4">
        <svg className="w-5 h-5 text-green-600" fill="currentColor" viewBox="0 0 20 20">
          <path fillRule="evenodd" d="M4 4a2 2 0 00-2 2v4a2 2 0 002 2V6h10a2 2 0 00-2-2H4zm2 6a2 2 0 012-2h8a2 2 0 012 2v4a2 2 0 01-2 2H8a2 2 0 01-2-2v-4zm6 4a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" />
        </svg>
        <h3 className="text-lg font-semibold text-gray-900">Promotional Offers</h3>
      </div>

      {/* Promo Code Input */}
      <div className="mb-6">
        <div className="flex gap-2">
          <input
            type="text"
            value={enteredCode}
            onChange={(e) => {
              setEnteredCode(e.target.value.toUpperCase());
              setError('');
            }}
            placeholder="Enter promo code"
            className="flex-1 border border-gray-300 rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            disabled={!!appliedPromo}
          />
          {appliedPromo ? (
            <button
              onClick={handleRemoveCode}
              className="bg-red-600 text-white px-4 py-2 rounded-lg hover:bg-red-700 transition-colors"
            >
              Remove
            </button>
          ) : (
            <button
              onClick={handleApplyCode}
              className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors"
            >
              Apply
            </button>
          )}
        </div>
        
        {error && (
          <p className="text-red-600 text-sm mt-2">{error}</p>
        )}
        
        {appliedPromo && (
          <div className="mt-2 p-3 bg-green-50 border border-green-200 rounded-lg">
            <div className="flex items-center justify-between">
              <div>
                <span className="text-green-800 font-medium">
                  {appliedPromo.code} applied!
                </span>
                <p className="text-green-700 text-sm">{appliedPromo.description}</p>
              </div>
              <div className="text-green-800 font-bold">
                -₹{discount}
              </div>
            </div>
            
            {appliedPromo.reviewIncentive?.enabled && (
              <div className="mt-2 p-2 bg-blue-50 border border-blue-200 rounded">
                <p className="text-blue-800 text-sm">
                  🎁 {appliedPromo.reviewIncentive.description}
                </p>
              </div>
            )}
          </div>
        )}
      </div>

      {/* Available Promo Codes */}
      {promoCodes.length > 0 && (
        <div>
          <h4 className="text-md font-medium text-gray-900 mb-3">
            Available Offers ({promoCodes.length})
          </h4>
          
          <div className="space-y-3">
            {displayedCodes.map((promo) => (
              <div
                key={promo.id}
                className={`border border-gray-200 rounded-lg p-4 ${
                  appliedPromo?.id === promo.id 
                    ? 'bg-green-50 border-green-300' 
                    : 'hover:border-gray-300'
                }`}
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center gap-2 mb-2">
                      <span className="bg-orange-100 text-orange-800 text-sm font-bold px-3 py-1 rounded-full">
                        {formatDiscount(promo)}
                      </span>
                      
                      {promo.firstTimeOnly && (
                        <span className="bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-full">
                          First time only
                        </span>
                      )}
                      
                      {isCodeExpiringSoon(promo) && (
                        <span className="bg-red-100 text-red-800 text-xs px-2 py-1 rounded-full">
                          Expires soon
                        </span>
                      )}
                    </div>
                    
                    <h5 className="font-medium text-gray-900 mb-1">
                      {getDiscountDescription(promo)}
                    </h5>
                    
                    <div className="text-sm text-gray-600 space-y-1">
                      <p>Code: <span className="font-mono font-medium">{promo.code}</span></p>
                      <p>Valid until: {new Date(promo.validTo).toLocaleDateString()}</p>
                      
                      {/* Usage indicator */}
                      <div className="flex items-center gap-2">
                        <span>Used: {promo.usedCount}/{promo.usageLimit}</span>
                        <div className="flex-1 h-2 bg-gray-200 rounded-full overflow-hidden max-w-20">
                          <div
                            className="h-full bg-orange-400 transition-all"
                            style={{ width: `${getUsagePercentage(promo)}%` }}
                          />
                        </div>
                      </div>
                    </div>
                    
                    {promo.reviewIncentive?.enabled && (
                      <div className="mt-2 p-2 bg-yellow-50 border border-yellow-200 rounded text-sm">
                        <span className="text-yellow-800">
                          🎁 {promo.reviewIncentive.description}
                        </span>
                      </div>
                    )}
                  </div>
                  
                  <div className="ml-4 flex flex-col gap-2">
                    <button
                      onClick={() => copyToClipboard(promo.code)}
                      className={`px-3 py-1 text-sm rounded transition-colors ${
                        copiedCode === promo.code
                          ? 'bg-green-100 text-green-800'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                      }`}
                    >
                      {copiedCode === promo.code ? 'Copied!' : 'Copy'}
                    </button>
                    
                    <button
                      onClick={() => {
                        setEnteredCode(promo.code);
                        setError('');
                      }}
                      className="px-3 py-1 text-sm bg-blue-100 text-blue-800 rounded hover:bg-blue-200 transition-colors"
                      disabled={!!appliedPromo}
                    >
                      Use Code
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
          
          {/* Show More/Less Button */}
          {promoCodes.length > 3 && (
            <div className="mt-4 text-center">
              <button
                onClick={() => setShowAllCodes(!showAllCodes)}
                className="text-blue-600 hover:text-blue-800 hover:underline font-medium"
              >
                {showAllCodes 
                  ? `Show less offers`
                  : `Show ${promoCodes.length - 3} more offers`
                }
              </button>
            </div>
          )}
        </div>
      )}

      {/* No Promo Codes */}
      {promoCodes.length === 0 && (
        <div className="text-center py-6">
          <svg className="w-12 h-12 text-gray-400 mx-auto mb-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 7h.01M7 3h5c.512 0 1.024.195 1.414.586l7 7a2 2 0 010 2.828l-7 7a2 2 0 01-2.828 0l-7-7A1.994 1.994 0 013 12V7a4 4 0 014-4z" />
          </svg>
          <p className="text-gray-600">No active promotional offers available</p>
          <p className="text-gray-500 text-sm mt-1">Check back later for new deals!</p>
        </div>
      )}

      {/* Additional Benefits */}
      <div className="mt-6 pt-4 border-t border-gray-200">
        <div className="text-sm text-gray-600">
          <h5 className="font-medium text-gray-900 mb-2">Earn more discounts:</h5>
          <ul className="space-y-1">
            <li>• Write a product review to get exclusive discount codes</li>
            <li>• Verified purchase reviews earn higher rewards</li>
            <li>• Become a top reviewer for early access to promotions</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

// Compact Promo Code Banner for Product Pages
interface PromoCodeBannerProps {
  productId: string;
  className?: string;
}

export const PromoCodeBanner: React.FC<PromoCodeBannerProps> = ({
  productId,
  className = ''
}) => {
  const [promoCodes, setPromoCodes] = useState<PromoCode[]>([]);

  useEffect(() => {
    const codes = getPromoCodeByProduct(productId).slice(0, 2); // Show top 2 offers
    setPromoCodes(codes);
  }, [productId]);

  if (promoCodes.length === 0) return null;

  return (
    <div className={`bg-gradient-to-r from-orange-50 to-yellow-50 border border-orange-200 rounded-lg p-4 ${className}`}>
      <div className="flex items-start gap-3">
        <div className="bg-orange-100 p-2 rounded-full">
          <svg className="w-4 h-4 text-orange-600" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M4 4a2 2 0 00-2 2v4a2 2 0 002 2V6h10a2 2 0 00-2-2H4zm2 6a2 2 0 012-2h8a2 2 0 012 2v4a2 2 0 01-2 2H8a2 2 0 01-2-2v-4zm6 4a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" />
          </svg>
        </div>
        
        <div className="flex-1">
          <h4 className="font-medium text-orange-900 mb-1">Special Offers Available!</h4>
          <div className="space-y-1">
            {promoCodes.map((promo) => (
              <div key={promo.id} className="text-sm text-orange-800">
                <span className="font-bold">{promo.code}:</span> {promo.description}
              </div>
            ))}
          </div>
          {promoCodes.length > 0 && (
            <p className="text-xs text-orange-700 mt-2">
              Apply code at checkout or scroll down to see all offers
            </p>
          )}
        </div>
      </div>
    </div>
  );
};

export default PromoCodeDisplay;
